<template>
    <div id= "devicePage">
    <div class="title">智能安防设备管理</div>
    <div class="devicepage-main">
      <div class="main-left">
        <div class="tree-search-box">
          <el-autocomplete v-model="treeSearchValue"
                    :fetch-suggestions="querySearchAsync"
                    @select="handleSelect"
                    placeholder="查询">
                    <i slot="suffix" 
                         class="el-input__icon el-icon-search" 
                         style="font-size:18px;cursor:pointer;">
                    </i>
          </el-autocomplete>
        </div>
        <div style="box-sizing: border-box;padding: 10px;">
          <select-group platform="monitor" 
                        ref="menu-tree"
                        @monitorLinkage="handleTreeClick">
          </select-group>
        </div>
      </div>
      <div class="main-right">
        <!-- 顶部操作栏 -->
        <!-- <div class="operationbox">
          <div class="searchbox">
            <el-form ref="searchform" :model="searchform" class="searchform" :inline="true">
                <el-form-item>
                    <el-input placeholder="设备名称" 
                              v-model="searchform.name"
                              clearable 
                              class="search-input"
                              @keyup.enter.native="searchtabledata('search')">
                      <i slot="suffix" 
                         class="el-input__icon el-icon-search" 
                         style="font-size:18px;cursor: pointer;"
                         @click="searchtabledata('search')"></i>
                    </el-input>
                </el-form-item>
            </el-form>
          </div>
          <div class="buttonbox">
            <div v-for="(item,index) in operationProperty"
                 :key="index"
                 :class="filterOperationClass(index)"
                 @click="filterClick(index)"
                 :title="filterOperationTitle(index)">
            </div>
          </div>
        </div> -->
        <!-- 表格 -->
        <div class="table-box" ref="tableBox">
            <el-table
                ref="devicetable"
                :data="tableData"
                height="500"
                v-loading="tableBoxLoading"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'rgba(245,248,252,1)'}"
                :row-key="getRowKeys"
                >
                <el-table-column
                    fixed
                    type="selection"
                    width="55"
                    reserve-selection
                ></el-table-column>
                <el-table-column
                  type="index"
                  width="50"
                  label="序号"
                  align="center">
                </el-table-column>
                <el-table-column
                    v-for="(item,index) in tableDataLabel"
                    :key="index"
                    :label="item.name"
                    :prop="item.property"
                    align="center"
                    :width="item.width"
                >
                  <!-- :formatter="index == 2? filterTbale: (index == 1 ?filterTbale2 : null )" -->
                <template slot-scope="scope">
                  <i v-if="index==2&&scope.row.status ==1"
                     class="status-icon status-disconnected"></i>
                  <i v-if="index==2&&scope.row.status ==2"
                     class="status-icon status-normal"></i>
                  <span :title="index ==1?scope.row.ais:null">{{filterSlot(scope,index)}}</span>
                </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center"
                    width="140px"
                >
                <template slot-scope="scope">
                  <div v-for="(item,index) in tableIconProperty"
                       :key="index"
                       :style="{'margin-right': index == 0 ? '10px': '0','cursor':'pointer','display':'inline-block'}"
                       :class="[ index == 0 ? 'set-row' : 'delete-row']"
                       @click="filterRowClick(index,scope.row,scope.$index)">
                  </div> 
                </template>
                </el-table-column>
            </el-table>
            <div class="pagination-box">
                <el-pagination
                    ref="pagination"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,20]"
                    :page-size.sync="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
      </div>
    </div>
    <!-- 添加设备模态框 -->
    <el-dialog
        title="新增监控设备"
        :visible.sync="addDialogVisible"
        :before-close="handleAddClose"
        :close-on-click-modal="false"
        width="738px"
        class="addDiglog">
          <div class="addSelect">
            <div class="addSelect-info">设备分组：</div>
            <div class="addDiglog-select-box">
              <el-select v-for="(item,index) in selectGroupArr"
                        :key="index"
                        v-model="item.selected"
                        placeholder="全部"
                        style="width:144px;"
                        @change="findGroupAndDevice($event,index)"
                        clearable
                        >
                <el-option v-for="(i,no) in item.optionArr"
                          :key="no"
                          :label="i.groupName"
                          :value="i.groupNo">
                </el-option>
              </el-select>
              <el-input
              placeholder="设备名称"
              v-model="addSearchValue"
              class="addDiglog-search-box"
              :style="{width:this.selectGroupArr.length==1?'440px':'144px'}"
              clearable
              @keyup.enter.native="findCarmeras('search')">
              <i slot="suffix" 
                class="el-input__icon el-icon-search" 
                style="font-size:18px;cursor:pointer;">
              </i>
            </el-input>
            </div>
          </div>
          <div class="addDialog-table-box">
            <el-table
              ref="addTable"
              :data="addTableData"
              :row-key="addTableGetRowKeys"
              @selection-change="handleAddSelectionChange"
              height="350"
              style="width: 100%">
              <el-table-column
                type="selection"
                width="55"
                reserve-selection>
              </el-table-column>
              <el-table-column
                label="序号"
                align="center"
                type="index"
                width="240">
              </el-table-column>
              <el-table-column
                prop="name"
                label="设备"
                align="center">
              </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="add-pagination-box"
                 style="margin-top:20px;">
              <el-pagination
                class="add-pagination"
                @size-change="handleAddSizeChange"
                @current-change="handleAddCurrentChange"
                :current-page.sync="addCurrentPage"
                :page-sizes="[5,10,20]"
                :page-size.sync="addPageSize"
                layout="total, sizes, prev, pager, next"
                :total="addTotal">
              </el-pagination>
            </div>
            <div style="text-align:left;margin-top:10px;color:#92959A;">
              <span>已选中{{addSelectNum}}个设备</span>
            </div>
          </div>
          <div class="addButton-box">
            <el-button type="primary" @click="insertDevice(addmultiple)">确定</el-button>
          </div>
      </el-dialog>
      <!-- 设置模态框 -->
      <el-dialog
        :title="settingTitle"
        :visible.sync="settingDialogVisible"
        width="800px"
        :before-close="handleSetClose"
        class="setDialog">
        <el-form label-position="left" label-width="70px" :model="settingForm" class="form">
          <div style="display:flex;box-sizing: border-box;padding-left: 0.3rem;">
          <div class="setDialog-worktime">工作时间</div>
          <div class="content-set">
            <div
              v-for="(week, index) in allWeeks"
              :key="index">
              <div class="week-word">{{ week.name }}</div>
              <div
                class="week-line"
                v-for="(item, dex) in week.timeList"
                :key="dex">
                <el-row :gutter="20">
                  <el-col :span="18">
                    <el-row :gutter="20">
                      <el-col :span="16">
                        <el-slider
                          v-model="item.timeQj"
                          range
                          label
                          :format-tooltip="formatTimetip"
                          :max="144"
                        >
                        </el-slider>
                      </el-col>
                      <el-col
                        :span="8"
                        style="margin-top:3px"
                      >
                        <div class="show-time" style="margin-left:30px;padding-top:8px;">{{filterTimeInterVal(item.timeQj)}}</div>
                      </el-col>
                    </el-row>
                  </el-col>
                  <el-col
                    :span="6"
                    style="margin-top:3px"
                  >
                    <el-button
                      type="text"
                      size="mini"
                      @click="delTimeTip(index, dex)"
                      v-if="index != 0"
                    >删除</el-button>
                    <el-button
                      type="text"
                      size="mini"
                      @click="addTimeTip(index, dex)"
                      v-if="dex === 0"
                    >新增</el-button>
                    <el-button
                      type="text"
                      size="mini"
                      @click="tupTime(index, dex)"
                      v-if="index !== 0 && dex===0"
                    >同上</el-button>
                  </el-col>
                </el-row>
              </div>
            </div>
          </div>
          </div>
        </el-form>
        <div class="selectAis">
          <div class="selectAis-title">
            <div style="font-weight:600;">AI能力</div>
            <div style="color:#D4D4D4;font-weight:500;">选择AI能力：</div>
          </div>
          <div class="aisTag-box">
            <span v-for="(item,index) in Ais"
                  :key="index"
                  :class="{'ai-Tag':item,'active-Tag':item.active}"
                  @click="handleTagsclick(index)">
              {{item.name}}
            </span>
          </div>
        </div>
        <div style="text-align:right;box-sizing: border-box;padding-right: 0.5rem;">
          <el-button style="width: 96px;height:36px;line-height:10px;" 
                     @click="handleSetCancel">取消</el-button>
          <el-button type="primary"
                     @click='settingSubmit'
                     style="width: 96px;height:36px;line-height:10px;">确定</el-button>
        </div>
      </el-dialog>
    </div>
</template>
<script>
import APICONFIG from '../../../../../../config/api.js';
import selectGroup from "../../device_manage/select_group.vue";

export default {
  data() {
    return {
      netAPI: null,
      deviceGroupAPI: null,
      deviceAPI: null,
      batchdisabled: true,
      addDialogVisible: false,
      settingDialogVisible: false,
      tableBoxLoading: false,
      // 设备组上方搜索框参数
      treeSearchValue: '',
      treeSearchQueryList: [],
      selectedOneGroupNo: '',
      selectedGroupNo: '',
      selectGroupArr: [],
      timeout: null,
      settingIds: [], //参与设置id列表
      addSearchValue: '', //新增监控设备搜索值
      addList: [],
      addDiglogSelect1: '',
      addDiglogSelect2: '',
      addDiglogSelect3: '',
      addTableData: [], //新增监控表格数据
      addmultiple: [],  //新增监控多选数据
      addSelectNum: 0, //新增监控多选个数
      addCurrentPage: 1,
      addPageSize: 10,
      addTotal: 0,
      deviceIds: [],
      allDeviceTypeArr: [],
      tableData: [],
      multipleSelection: [],
      settingTitle: '',
      Ais: [],
      tableDataLabel: [
        { name: "设备名称", property: "name" },
        { name: "AI能力", property: "ais", minWidth: '300px' },
        { name: "设备状态", property: "status" },
      ],
      addSelectArr: [

      ],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      store: this.$store.default.state.MonitoringManage,
      // 操作栏图标路径及状态
      operationProperty: [
        {
          active: false,
          defaultUrl: require('./img/deviceManage/add_.png'),
          activeUrl: require('./img/deviceManage/add.png')
        },
        {
          active: false,
          defaultUrl: require('./img/deviceManage/batchDeletion_.png'),
          activeUrl: require('./img/deviceManage/batchDeletion.png')
        },
        {
          active: false,
          defaultUrl: require('./img/deviceManage/batchSetting_.png'),
          activeUrl: require('./img/deviceManage/batchSetting.png')
        },
      ],
      // 表格行图标路径及状态
      tableIconProperty: [
        {
          active: false,
          defaultUrl: require('./img/deviceManage/set.png'),
          activeUrl: require('./img/deviceManage/set_selected.png')
        },
        {
          active: false,
          defaultUrl: require('./img/deviceManage/delete.png'),
          activeUrl: require('./img/deviceManage/delete_selected.png')
        },
      ],
      getRowKeys(row) {
        return row.id
      },
      addTableGetRowKeys(row) {
        return row.id
      },
      allWeeks: [
        {
          name: "星期一",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期二",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期三",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期四",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期五",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期六",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期日",
          timeList: [{ timeQj: [0, 144] }]
        }
      ],
      searchform: {
        // deviceName: '',
        // deviceType: '',
      },
      addSearchform: {

      },
      createform: {

      },
      settingForm: {
        // inOut: 0
      },
    }
  },
  components: {selectGroup},
  computed: {
    searchName() {
      return this.searchform.name
    }
  },
  watch: {
    addDiglogSelect1: {
      handler(newVal, oldVal) {
        if(newVal == '') {
          this.addDiglogSelect2 = '';
          this.addDiglogSelect3 = '';
        }
      }
    },
    addDiglogSelect2: {
      handler(newVal, oldVal) {
        if(newVal == '') {
          this.addDiglogSelect3 = '';
        }
      }
    },
    addSearchValue: {
      handler(newVal, oldVal) {
        if(newVal == '') {
          this.findCarmeras('search')
        }
      }
    },
    searchName(newVal, oldVal) {
      if(newVal == '') {
        this.searchtabledata('search')
      }
    }
  },
  created() {
    this.netAPI = APICONFIG().get("monitorSystem").deviceManage;
    this.deviceGroupAPI = APICONFIG().get("device_group");
    this.deviceAPI = APICONFIG().get("device");
    this.searchtabledata();
    this.findHasDeviceGroups('1005');
    this.findAis()
  },
  mounted() {
  },
  methods: {
    filterSlot(scope, index) {
      // console.log(scope.row)
      // console.log(index)
      if(index == 0) {
        return scope.row.name
      }
      if(index == 1) {
        if(scope.row.ais) {
          return scope.row.ais.join(",").slice(0, 14) + '...'
        }
      }
      if(index == 2) {
        if(scope.row.status) {
          return scope.row.status == 1 ? '断开' : '正常'
        }
      }
    },
    findAis() {
      // this.netAPI.findAis
      this.Ais = []
      this.$http.get(this.netAPI.findAis, {
        params: {
          type: 1,
          rows: 10000
        }
      }).then(
        res => {
          if(res.body.status == 200) {
            // console.log(JSON.stringify(res.body.data))
            res.body.data.forEach(item => {
              this.Ais.push({
                name: item.name,
                active: false,
                id: item.id
              })
            })
          }
        }
      )
    },
    // 点击树
    handleTreeClick(data) {
      // console.log(data.groupNo)
      this.searchform.groupNo = data.groupNo;
      this.searchform.name = '';
      this.searchtabledata('search')
    },
    // 设备组搜索框远程搜索
    querySearchAsync(queryString, cb) {
      let promise = new Promise((resolve, reject) => {
        this.$http.get(this.netAPI.findGroup, {params: {groupName: queryString}}).then(
            res => {
              if(res.body.status == 200) {
                // console.log(JSON.stringify(res.body.data))
                res.body.data.forEach(item => {
                  item.value = item.groupName
                })
                resolve(res.body.data)
              }
            }
          )
      })
      promise.then(res => {
        let sugessQuery = res;
        // console.log(JSON.stringify(sugessQuery))
        let results = queryString ? sugessQuery.filter(this.createStateFilter(queryString)) : res;
        // console.log(JSON.stringify(results))
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 1000 * Math.random());
      })
    },
    // 远程搜索条件过滤
    createStateFilter(queryString) {
      return (state) => {
        return (state.groupName.indexOf(queryString) == 0);
      };
    },
    // 树形菜单搜索后点击事件
    handleSelect(item) {
      console.log(JSON.stringify(item))
      this.searchform.groupNo = item.groupNo;
      this.searchform.name = '';
      this.$refs['menu-tree'].initData();
      this.searchtabledata('search');
    },
    // 点击Ai能力Tag
    handleTagsclick(index) {
      this.Ais.forEach((item, i) => {
        if(i == index) {
          item.active = !item.active;
        }
      })
    },
    // 格式化table数据
    filterTbale(row, column, cell, index) {
      // console.log(JSON.stringify(row))
      switch(row.status) {
        case 1:
          return <div>
                  <i style="display:inline-block;height:10px;width:10px;background:#FFC62D;border-radius:50%;margin-right:8px;"></i>
                  <span>断开</span>
                 </div>
          // return h('div', [
          //   h('i', {attrs: {style: "display:inline-block;height:10px;width:10px;background:#FFC62D;border-radius:50%;margin-right:8px;"}}),
          //   h('span', '断开')
          // ])
        case 2:
          return <div>
                  <i style="display:inline-block;height:10px;width:10px;background:#67C23A;border-radius:50%;margin-right:8px;"></i>
                  <span>正常</span>
                 </div>;
        case 3:
          return "常开";
        case 4:
          return "常闭";
      }
    },
    filterTbale2(row) {
      if(row.ais) {
        return row.ais.slice(0, 3) + '...'
        // console.log(JSON.stringify(row.ais))
        // return <div title="row.ais">{row.ais.slice(0, 3).join(",") + '...'}</div>
      }
      // return row.ais ? h('div', {attrs: {title: row.ais}}, row.ais) : null
    },
    // 动态设置操作icon的类名
    filterOperationClass(index) {
      switch(index) {
        case 0:
          return 'addIcon-box';
        case 1:
          return 'deleteIcon-box';
        case 2:
          return 'setIcon-box';
        default:
          break;
      }
    },
    filterOperationTitle(index) {
      switch(index) {
        case 0:
          return '添加';
        case 1:
          return '批量删除';
        case 2:
          return '批量设置';
        default:
          break;
      }
    },
    // 处理点击顶部操作icon
    filterClick(index) {
      switch(index) {
        case 0:
          this.openAdd();
          break;
        case 1:
          this.deleteRow(this.multipleSelection);
          break;
        case 2:
          this.openSetting(this.multipleSelection, 'batch');
          break;
        default:
          break;
      }
    },
    // 处理点击每一行的操作icon
    filterRowClick(index, row, rowIndex) {
      switch(index) {
        case 0:
          this.openSetting(row, 'row');
          break;
        case 1:
          this.deleteRow(row);
          break;
        default:
          break;
      }
    },
    // 查询
    searchtabledata(v, data) {
      if(v == 'search') {
        this.currentPage = 1;
        this.addCurrentPage = 1;
      }
      let query = {};
      this.tableBoxLoading = true;
      // 如果没有传设备组编号，默认查询所有设备组下设备
      if(!this.searchform.groupNo) {
        this.searchform.groupNo = '1005';
      }
      this.searchform.pageSize = this.pageSize;
      this.searchform.pageNo = this.currentPage;
      query.params = this.searchform;
      this.$http.get(this.netAPI.find, query).then(
          res => {
            if (res.body.status == 200) {
              this.tableData = res.body.data
              // console.log(JSON.stringify(this.tableData))
              this.total = res.body.total
              this.tableBoxLoading = false;
            } else {
              this.$message.warning("查询失败" + res.body.message);
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        )
    },
    // 打开添加设备模态框
    openAdd() {
      // 查询所有设备
      this.findCarmeras();
      this.addDialogVisible = true;
    },

    findGroupsOne() {
      // 查询第一级设备组
      this.selectGroupArr = []
      this.$http.get(this.deviceGroupAPI.find).then(
        res => {
          if(res.body.status == 200) {
            // console.log(JSON.stringify(res.body.data))
            let groupsOne = [];
            res.body.data.forEach(item => {
              if(item.deviceNumber != 0) {
                groupsOne.push(item);
              }
            })
            // console.log(JSON.stringify(groupsOne))
            // 将有设备的设备组加入下拉框数组
            this.selectGroupArr.push(
              {
                selected: '',
                optionArr: groupsOne
              }
            )
          }
        }
      )
    },
    // 添加时查询所选设备组下级设备组以及所选设备组组下设备
    findGroupAndDevice(currNo, index) {
      // value为设备组编号
      console.log(JSON.stringify(currNo))
      console.log(JSON.stringify(index))
      this.findHasDeviceGroups(currNo, index);
      // 如果第一个下拉框选择为空，查询所有设备
      if(currNo == '' && index == 0) {
        this.findCarmeras('select', '1005')
      }
      // 如果非第一个下拉框选择为空,查询上级组下设备
      if(currNo == '' && index != 0) {
        this.findCarmeras('select', this.selectGroupArr[index].optionArr[0].parentNo)
      }
      // 如果选择了组号，查询选择组下的设备
      if(currNo != '' && currNo !== undefined) {
        this.findCarmeras('select', currNo)
      }
    },
    // 查询有设备的设备组
    findHasDeviceGroups(num, index) {
      if(num !== '') {
        this.$http.get(this.deviceGroupAPI.find, {
          params: {
            groupNo: num
          }
        }).then(
          res => {
            if(res.body.status == 200) {
              // 如果查询出的设备组数大于0,判断每一个设备组下有无设备
              if(res.body.data.length > 0) {
                // console.log(1122)
                let groups = [];
                // 有设备的话才添加到下拉框数组
                res.body.data.forEach(item => {
                  if(item.deviceNumber > 0) {
                    // 组下摄像机数量判断,有摄像机的才添加到待添加下拉框数组里(需更改，目前判断的是组下所有设备)
                    groups.push(item);
                  }
                })
                if(groups.length > 0) {
                // 如果已有同级设备组，先进行替换
                  if(this.selectGroupArr[index + 1]) {
                    this.selectGroupArr.splice(index + 1, this.selectGroupArr.length - 1)
                  }
                // 将有设备的设备组加入下拉框数组
                  this.selectGroupArr.push(
