    },
    // 查询摄像机
    findCarmeras(str, data) {
      // console.log(JSON.stringify(data))
      // console.log(JSON.stringify(str))
      // 如果是搜索框搜索的时候，置空已选择设备组
      if(str == 'search') {
        this.addCurrentPage = 1
      }
      // 如果是下拉框查询，查询设备组编号等于传过来的值
      if(str == 'select') {
        this.addCurrentPage = 1
        // 如果传过来的值是编号才加入搜索字段
        if(data != false) {
          console.log('searchCamerasHasGroupNo')
          this.addSearchform.groupNo = data
        }else{
          delete this.addSearchform.groupNo
        }
      }
      // 如果搜索框有值
      console.log('findCameras')
      console.log(111)
      console.log(this.addSearchValue)
      // if(this.addSearchValue) {
      //   this.addSearchform.name = this.addSearchValue;
      // }
      this.addSearchform.name = this.addSearchValue
      this.addSearchform.type = 1;
      this.addSearchform.pageSize = this.addPageSize;
      this.addSearchform.pageNo = this.addCurrentPage;
      // console.log(JSON.stringify(this.addSearchform))
      this.$http.get(this.deviceAPI.find, {params: this.addSearchform}).then(
        res => {
          if(res.body.status == 200) {
            // console.log(JSON.stringify(res.body.data))
            this.addTableData = res.body.data
            this.addTotal = res.body.total
          }
        }
      )
    },
    // 关闭添加设备
    handleAddClose(done) {
      done();
    },
    //添加选择设备的tag
    handleEdit(index, row) {
      console.log(index);
      row.isAdd = true;
      this.$set(this.store.deviceList, index, row);
      this.addList.push({ name: row.name, id: row.id });
    },
    // 删除选择的设备tag
    deleteTag(tag) {
      // console.log(tag);
      this.addList.splice(this.addList.indexOf(tag), 1);
      for (let index = 0; index < this.store.deviceList.length; index++) {
        const element = this.store.deviceList[index];
        if (tag.id == element.id) {
          element.isAdd = false;
          this.$set(this.store.deviceList, index, element);
        }
      }
    },
    // 确定添加设备
    insertDevice(row) {
      console.log(JSON.stringify(row))
      // this.createform.devices = [ {"deviceId": 26, "name": "设备1"} ];
      this.createform.devices = [];
      row.forEach(item => {
        this.createform.devices.push({
          deviceId: item.id,
          name: item.name
        })
      })
      console.log(JSON.stringify(this.createform.devices))
      this.$http.post(this.netAPI.insert, this.createform).then(
        res => {
          if (res.body.status == 200) {
            // console.log(2222222)
            console.log(JSON.stringify(res.body.data))
            this.$message.success("添加成功");
            this.$refs.addTable.clearSelection();
            this.searchtabledata();
          } else {
            // console.log(33333)
            this.$message.warning("添加失败" + res.body.message);
          }
          this.addDialogVisible = false;
        },
        err => {
          // console.log(44444)
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
          this.addDialogVisible = false;
        }
      )
    },
    // 删除行
    deleteRow(row) {
      console.log(JSON.stringify(row))
      let ids = [];
      if (!row) {
        this.$message({
          message: "请选中要删除设备",
          type: "warning"
        });
        return;
      }
      //判断类型
      let rowType = row instanceof Array
      if (rowType == true) {
        for (let index in row) {
          ids.push({ id: row[index].id });
        }
      } else {
        ids.push({ id: row.id });
      }
      // console.log(JSON.stringify(ids))
      if(ids.length > 0) {
        this.$confirm("确定要删除选中的摄像机？", "删除提示", {
          distinguishCancelAndClose: true,
          type: 'warning',
          confirmButtonText: "删除",
          cancelButtonText: "取消"
        }).then(() => {
          this.$http.post(this.netAPI.delete, ids).then(
          res => {
            if (res.data.status == 200) {
              console.log(JSON.stringify(res.body))
              if (ids.length == this.tableData.length) {
                this.currentPage = this.currentPage - 1 > 0 ? this.currentPage - 1 : 1
                this.searchtabledata();
              } else {
                this.searchtabledata();
              }
              this.$message.success("删除成功!");
              this.$refs.devicetable.clearSelection();
            } else {
              this.$message.warning(
                  "服务错误" + res.body.message
                );
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        );
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }else{
        this.$message.warning('请选择要删除的设备!')
      }
    },
    // 新增工作时间段
    addTimeTip(dex1, dex2) {
      if (this.allWeeks[dex1].timeList.length < 4) {
        this.$set(
          this.allWeeks[dex1].timeList,
          `${this.allWeeks[dex1].timeList.length}`,
          { timeQj: [0, 144] }
        );
      } else {
        this.$message.error("每日上限四个时间段");
      }
    },
    // 删除工作时间段
    delTimeTip(dex1, dex2) {
      this.allWeeks[dex1].timeList.splice(dex2, 1);
    },
    // 同步昨天的时间段设置
    tupTime(dex1, dex2) {
      this.allWeeks[dex1].timeList = JSON.parse(
        JSON.stringify(this.allWeeks[dex1 - 1].timeList)
      );
    },
    // 格式化设备时间
    formatTimetip(val) {
      let mintime = `${
        (val % 6) * 10 < 10 ? "0" + (val % 6) * 10 : (val % 6) * 10
      }`;
      return `${Math.floor(val / 6)}:${mintime}`;
    },
    // 处理所选时间区间
    filterTimeInterVal(val) {
      let start = val[0];
      let end = val[1];
      let minStart = `${
        (start % 6) * 10 < 10 ? "0" + (start % 6) * 10 : (start % 6) * 10
      }`;
      let minEnd = `${
        (end % 6) * 10 < 10 ? "0" + (end % 6) * 10 : (end % 6) * 10
      }`;
      return `${Math.floor(start / 6)}:${minStart}-${Math.floor(
        end / 6
      )}:${minEnd}`;
    },
    // 打开批量设置或设置
    openSetting(row, str) {
      console.log(JSON.stringify(row))
      let ids = [];
      //判断类型
      let rowType = row instanceof Array
      if (rowType == true) {
        for (let index in row) {
          // ids.push({ id: row[index].id });
          ids.push(row[index].id)
        }
      } else {
        // ids.push({ id: row.id });
        ids.push(row.id)
      }
      console.log(JSON.stringify(ids))
      if(ids.length < 1) {
        this.$message.warning('请选择要批量设置的设备!')
      }else{
        this.settingIds = ids;
        this.settingDialogVisible = true;
      }
      // 如果是批量设置，重置AI能力选择状态和工作时间
      if(str == 'batch') {
        this.settingTitle = '批量设置';
      // 把AI能力全部设置成未选中
        this.Ais.forEach(item => {
          item.active = false;
        })
      // 重置工作时间
        this.allWeeks.forEach(item => {
          item.timeList = [{ timeQj: [0, 144] }]
        })
      }else{
        this.settingTitle = '设置';
        // 如果是单个设置，则展示原有设置
        if(row.ais) {
          // 先全部置空
          this.Ais.forEach(item => {
            item.active = false;
          })
          // 展示已经设置的Ai能力
          row.ais.forEach(item => {
            this.Ais.forEach(ai => {
              if(item == ai.name) {
                ai.active = true
              }
            })
          })
        }else{
          this.Ais.forEach(item => {
            item.active = false;
          })
        }
        // 展示已经设置的时间
        this.timeDataTransfer(row);
      }
    },
    // 已设置时间数据格式化
    timeDataTransfer(initData) {
      this.allWeeks = [
        {
          name: "星期一",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期二",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期三",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期四",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期五",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期六",
          timeList: [{ timeQj: [0, 144] }]
        },
        {
          name: "星期日",
          timeList: [{ timeQj: [0, 144] }]
        }
      ]
      let xqs = ["mon", "tue", "wed", "thu", "fri", "sat", "sun"];
      let rowData = initData;
      var a = true
      // 时间段数据转换后复值
      xqs.forEach((xq, index) => {
        if(!rowData[xqs[index]]) {
          a = false
        }
      })
      // 如果点击的行数据内有xqs里的字段
      if(a) {
        this.allWeeks.forEach((item, index) => {
          if (this.allWeeks[index].timeList) {
            this.allWeeks[index].timeList = [];
          }
        });
      }
      xqs.forEach((xq, index) => {
        if (rowData[xqs[index]]) {
          rowData[xqs[index]].split(",").forEach((item, dex) => {
            let start =
              parseInt(item.split("-")[0].split(":")[0]) * 6 +
              parseInt(item.split("-")[0].split(":")[1]) / 10;
            let end =
              parseInt(item.split("-")[1].split(":")[0]) * 6 +
              parseInt(item.split("-")[1].split(":")[1]) / 10;
            this.allWeeks[index].timeList.push({ timeQj: [start, end] });
          });
        }
      });
    },
    // 关闭批量设置模态框
    handleSetClose(done) {
      done();
      this.settingIds = [];
    },
    // 确认设置
    settingSubmit() {
      let weeks = JSON.parse(JSON.stringify(this.allWeeks));
      weeks.forEach((item, dex1) => {
        item.timeList.forEach((res, dex2) => {
          let start = res.timeQj[0];
          let end = res.timeQj[1];
          let minStart = `${
            (start % 6) * 10 < 10 ? "0" + (start % 6) * 10 : (start % 6) * 10
          }`;
          let minEnd = `${
            (end % 6) * 10 < 10 ? "0" + (end % 6) * 10 : (end % 6) * 10
          }`;
          weeks[dex1].timeList[dex2].timeQj = `${Math.floor(
            start / 6
          )}:${minStart}-${Math.floor(end / 6)}:${minEnd}`;
        });
      });
      let xqs = ["mon", "tue", "wed", "thu", "fri", "sat", "sun"];
      xqs.forEach((xq, index) => {
        let str = "";
        weeks[index].timeList.forEach(res => {
          if (str) {
            str = str + "," + res.timeQj;
          } else {
            str = res.timeQj;
          }
        });
        this.settingForm[xq] = str;
      });
      this.settingForm.ids = this.settingIds;
      if (this.settingIds.length == 0) {
        console.log(JSON.stringify(this.multipleSelection));
        var ids = [];

        for (const key in this.multipleSelection) {
          if (this.multipleSelection.hasOwnProperty(key)) {
            const element = this.multipleSelection[key];
            ids.push(element.id);
          }
        }
        this.settingForm.ids = ids;
      }
      this.settingForm.ais = [];
      // 选择ai能力
      this.Ais.forEach(item => {
        if(item.active) {
          this.settingForm.ais.push(item.name)
        }
      })
      // console.log(JSON.stringify(this.settingIds));
      console.log(JSON.stringify(this.settingForm));
      this.$http.post(this.netAPI.update, this.settingForm).then(
        res => {
          if (res.body.status == 200) {
            // console.log(2222222)
            console.log(JSON.stringify(res.body.data))
            this.$message.success("设置成功");
            this.searchtabledata();
          } else {
            // console.log(33333)
            this.$message.warning("设置失败" + res.body.message);
          }
          this.settingDialogVisible = false;
          this.$refs.devicetable.clearSelection();
        },
        err => {
          // console.log(44444)
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
          this.settingDialogVisible = false;
          this.$refs.devicetable.clearSelection();
        }
      )
    },
    // 取消设置
    handleSetCancel() {
      this.settingDialogVisible = false;
    },
    handleSelectionChange(val) {
      if(val.length > 0) {
        this.batchdisabled = false
      }else{
        this.batchdisabled = true
      }
      this.multipleSelection = val;
    },
    //新增监控多选处理
    handleAddSelectionChange(val) {
      // console.log(JSON.stringify(val))
      this.addmultiple = val;
      this.addSelectNum = val.length;
    },
    handleSizeChange (pageSize) {
      this.pageSize = pageSize;
      this.searchtabledata();
    },
    handleAddSizeChange(pageSize) {
      this.addPageSize = pageSize;
      this.addCurrentPage = 1;
      this.findCarmeras();
    },
    handleCurrentChange (Current) {
      this.currentPage = Current;
      this.searchtabledata();
    },
    handleAddCurrentChange(Current) {
      this.addCurrentPage = Current;
      this.findCarmeras();
    }
