        background: #F5F8FC;
        height: 80px;
        line-height: 80px;
        font-size: 20px;
        box-sizing: border-box;
        font-family: Microsoft YaHei;
        display: flex;
        align-items: center;
    }
     .devicepage-main{
       position: absolute;
       top: 80px;
       bottom: 0;
       left: 0;
       right: 0;
       padding-bottom: 0;
       display: flex;
        .main-left{
        width: 220px;
        min-width: 190px;
        border: 1px solid #dedede;
        background: #fff;
        box-shadow:#E9E9E9 0 2px 4px 0;
        margin-right: 15px;
        @media screen and(max-width:1366px){
          width: 190px;
        }
      }
      .main-right{
        flex: 1;
        border: 1px solid #dedede;
        box-sizing: border-box;
        background: #fff;
        box-shadow:#E9E9E9 0 2px 4px 0;
        padding: 20px 30px;
        // flex: 1;
        // background: #fff;
        .operationbox{
            display: flex;
            box-sizing: border-box;
            padding-right: 0.1rem;
            justify-content: space-between;
            .searchbox{
              display: flex;
              align-items: center;
              .searchform{
                .search-input{
                  width: 320px;
                  height: 40px;
                }
              }
            }
            .buttonbox{
              width: 100px;
              display: flex;
              justify-content: space-between;
              align-items: center;
              >div{
                cursor: pointer;
                width: 24px;
                height: 24px;
              }
              .addIcon-box{
                background: url('./img/deviceManage/add_.png') no-repeat;
                &:hover {
                  background: url('./img/deviceManage/add.png')  no-repeat!important;
                }
              }
              .deleteIcon-box{
                background: url('./img/deviceManage/batchDeletion_.png') no-repeat;
                &:hover {
                  background: url('./img/deviceManage/batchDeletion.png')  no-repeat!important;
                }
              }
              .setIcon-box{
                background: url('./img/deviceManage/batchSetting_.png') no-repeat;
                &:hover {
                  background: url('./img/deviceManage/batchSetting.png')  no-repeat!important;
                }
              }
            }
        }
        .table-box{
            .status-icon{
              display: inline-block;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              margin-right: 6px;
            }
            .status-normal{
              background: #67C23A;
            }
            .status-disconnected{
              background: #FFC62D;
            }
            .pagination-box {
              height: 64px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
            .set-row{
              background: url('./img/deviceManage/set.png') no-repeat;
              height: 30px;
              width: 54px;
              &:hover {
                background: url('./img/deviceManage/set_selected.png')  no-repeat!important;
                height: 30px;
              }
            }
            .delete-row{
              background: url('./img/deviceManage/delete.png') no-repeat;
              height: 30px;
              width: 54px;
              &:hover {
                background: url('./img/deviceManage/delete_selected.png')  no-repeat!important;
                height: 30px;
              }
            }
        }
      }
     }
     .addDiglog{
       .add-pagination{
         text-align: center;
       }
     }
     .setDialog{
       .selectAis{
         margin-top: 0.4rem;
         margin-bottom: 0.2rem;
         box-sizing: border-box;
         padding-left: 0.3rem;
         .selectAis-title{
           flex: 0.6;
           height: 50px;
           font-size: 14px;
           font-family: MicrosoftYaHei;
           font-weight: 600;
           >div:nth-child(1){
             margin-bottom: 5px;
           }
         }
         .aisTag-box{
           flex: 4;
           display: flex;
           flex-wrap: wrap;
           box-sizing: border-box;
           padding-left: 1.7rem; 
           padding-right: 0.6rem;
           margin-bottom: 20px;
          .ai-Tag{
              width: 114px;
              height: 40px;
              margin: 3px 4px;
              display: inline-block;
              color: #92959A;
              font-family: MicrosoftYaHei;
              border: 1px solid #D4D4D4;
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 5px;
              cursor: pointer;
              &:hover{
              //   background: rgba(236,245,255,1);;
              //   color: #98C0EC;
                // border:1px solid rgba(212,230,251,1);
                color: #2D8CF0;
                border: 1px solid #2D8CF0;
              }
          }
          .active-Tag{
            color: #fff;
            background: #2D8CF0;
            border: 1px solid #2D8CF0;
            &:hover{
                color: #fff;
            //     background: #2D8CF0;
            //     border: 1px solid #2D8CF0;
              }
          }
         }
       }
     }
 }

</style>
<style lang="scss">
#devicePage{
  .tree-search-box{
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 10px;
    >div{
      width: 100%;
        .el-input__inner{
        height: 48px;
        width: 100%;
        border-radius: 0;
        border: none;
        border-bottom: 1px solid #E9EAEB;
      }
    }
    
  }
  .searchform{
    .el-form-item{
      margin-bottom: 15px;
      .search-input{
        .el-input__inner{
          border-radius: 20px;
        }
      }
    }
  }
  .el-table th, .el-table tr {
      color:rgba(37,44,53,1);
    }
  .el-dialog__header{
    font-family: Microsoft YaHei;
  }
  .addDiglog{
    .el-dialog__header{
        border-bottom: 1px solid #E9EAEB;
      }
    .el-dialog__body{
      padding: 30px;
      padding-top: 15px;
      // .addDiglog-search-box{
      //   margin-bottom: 20px;
      //  }
       .addSelect{
         display: flex;
        .addSelect-info{
          min-width: 80px;
          line-height: 40px;
        }
        .el-input__suffix{
          height: 40px;
        }
        .addDiglog-select-box{
            display: flex;
            flex-wrap: wrap;
            .el-select{
              margin-right: 5px;
              margin-bottom: 10px;
            }
          }
       }
      .addButton-box{
        width: 100%;
        text-align: right;
        margin-top: 10px;
        >button{
          padding: 10px 27px;
        }
      }
    }
  }
  .setDialog{
    .el-dialog__body{
      padding-top: 10px;
      .setDialog-worktime{
        height: 30px;
        flex: 0.6;
        font-size: 14px;
        font-family: MicrosoftYaHei;
        font-weight: 600;
        display: flex;
        align-items: center;
      }
      .content-set{
        flex: 4;
        margin-top: 10px;
      }
    }
 }
 .el-table__fixed::before{
   background: #fff;
 }
 .el-table::before{
   background: #fff;
 }
}
</style>





